body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body,
html,
#root {
  height: 100%;
}

html {
  font-size: 18px;
}

.PageTop {
  padding-top: 2rem;
  padding-bottom: 1rem;
  background-color: #31373d;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-size: 2rem;
  color: #fff;
  margin-top: 0;

}

.PageTitle{
  padding: 0;
  margin: 0;
}

.PageTitle span {
  color:#7d8b93;
}

.PageTop p {
  font-weight: lighter;
  font-size: 1rem;
  padding-bottom: 1rem;
}

h2 {
  font-family: 'Poppins', sans-serif;
  font-size: 1.55rem;
}

.App {
  height: 100%;
  padding: 0 17% 10% 17%;
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: black;
}

.App-playlist {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

@media only screen and (max-width: 1020px) {
  .App-playlist {
    align-items: center;
    flex-direction: column;
  }
}

/*SearchBar Styling*/
.SearchBar {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
  margin-bottom: 3.5rem;
  padding: 0;
}

.SearchBar input {
  width: 350px;
  padding: .88rem 0;
  border: 1px solid #31373d;
  border-radius: 3px;
  color: #31373d;
  text-align: center;
  font-size: 1rem;
}

.SearchBar input:focus {
  outline: none;
}

