.Main {
  display: flex;
}

.ArticleList{
  padding-left: 5%;
  padding-right: 5%
}

.SearchResults {
  width: 50%;
  height: 950px;
  overflow-y: scroll;
  padding: .88rem;
  background-color: #31373d;
  box-shadow: 0 4px 2px 2px #000000;
  color: white;
  margin-right: 10%;
  margin-top: 0;
  margin-bottom:0;
}

h2{
  border-bottom: 1px solid white;
}

.TotalHits {
  color:#7d8b93;
}

.Hole {
  width: 50%;
  height: 950px;
  overflow-y: scroll;
  padding: .88rem;
  background-color: #31373d;
  box-shadow: 0 4px 2px 2px #000000;
  color: white;
  margin-left: 10%;
}

.Article {
  border-bottom: 1px solid white;
  display: flex;
}

.Article button {
  border: none;
  background-color:#31373d;
  color: white;
  font-size: 2rem;
  padding: 5%;
  font-weight:lighter;
  cursor: pointer;
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

@media only screen and (max-width: 1020px) {
  .SearchResults {
    width: 90%;
    margin-bottom: 2rem;
  }
}

.Result-snippet {
  font-weight: lighter;
  margin-bottom: 1rem;
  margin-top: 0;
  text-align: justify;
}

.Article-title{
  margin-bottom:0.5rem;
}

.Article-information{
  margin-bottom: 1.5rem;
}

.ArticleButton {
  background-color: white;
  border-radius: 1rem;
  padding: 0.3rem;
  text-decoration: none;
  color:#31373d;
  font-size: 0.8rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

.ArticleButton:hover{
  background-color:#7d8b93;
  color: white;
}